a {
  padding: 5px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  margin: auto;
}

a:active,
a:focus {
  background-color: #4CAF50 !important;
  color: white;
}

a:hover:not(.active) {
  background-color: #ddd;
}


